import { useContext } from '@nuxtjs/composition-api';
import { computed, defineComponent, ref, watch } from '@vue/composition-api';
export default defineComponent({
    props: {
        mobileAttr: {
            type: Object,
            default: null
        },
        desktopAttr: {
            type: Object,
            default: null
        },
        width: {
            type: Number,
            default: 600
        },
        title: {
            type: String,
            default: ''
        },
        value: {
            type: Boolean,
            default: false
        }
    },
    setup(props, { emit }) {
        const { $vuetify } = useContext();
        const isMobile = computed(() => $vuetify.breakpoint.mobile);
        const popupValue = ref(false);
        watch(popupValue, () => {
            if (!popupValue.value)
                emit('onClose');
        });
        watch(() => props.value, () => {
            popupValue.value = props.value;
        });
        return {
            isMobile,
            popupValue
        };
    }
});
